.link_general {
    text-decoration: none;
    color: black;
}
.link_current {
    text-decoration-line: underline;
    text-decoration-color: black;
    text-decoration-thickness: 2px;
    color: black;
}

.screen_size {
    width: 100vw; height: 100vh;
}
.flex_container_row {
    display: flex;
    flex-direction: row;
}
.flex_container_col {
    display: flex;
    flex-direction: column;
}
.section_size {
    flex-grow: 1;
}

.mainpage {
    width: 100vw; height: 100vh;
    display: flex;
    flex-direction: column;
}
.mainpage_section_v1 {
    flex-grow: 1;
}
.mainpage_section_v2 {
    flex-grow   : 1;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.homepage_title {
    width: 100%; height: 100px; margin-top: 100px;
    text-align: center;
    line-height: 100px;
    font-size: 100px;
    letter-spacing: 10px;
}
.homepage_mainImage {
    display: block;
    width: 400px; height: 400px; margin: 0px auto;
}
.homepage_login {
    width: 100%; height: 60px;
    text-align: center;
    line-height: 60px;
    font-size: 60px;
}

.nav_box {
    width: 100%; height: 100px;
    position: relative;
}
.left_nav {
    position: absolute;
    left: 0px;
    display: flex;
}
.right_nav {
    position: absolute;
    right: 0px;
    display: flex;
}

.nav_mainImage {
    width: 50px; height: 50px; margin: 25px;
}

.menu_box {
    height: 100px;
    margin: 0px 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.menu_name {
    font-size: 30px;
}
.menu_name:hover {
    cursor: pointer;
}

.content_item_box {
    width: 320px; height: 600px;
    border: 2px solid black;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.content_title {
    text-align: center;
    font-size: 50px;
}
.content_box {
    width: 95%; height: 90%; margin: 0px auto; 
    border: 2px solid black;
    border-radius: 20px;
}

.about_title {
    height: 100px; margin-left: 30px;
    line-height: 100px;
    font-size: 50px;
}
.about_text {
    height: 200px; margin-left: 30px;
    font-size: 30px;
}
.about_contact_box {
    height: 50px; margin: 0px 30px 20px;
    line-height: 50px;
    font-size: 30px;
    display: flex;
}
.about_contact_name {
    width: 200px;
    flex-grow: 1;
}
.about_contact_email {
    flex-grow: 4;
}
.about_contact_part {
    flex-grow: 0;
}